* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.navi-bar {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.navi-menu {
  margin: 0;
  list-style: none;
  display: flex;
}

.navi-item {
  margin: 0;
  padding-left: 20px;
}

.navi-item:first-child {
  padding: 0%;
}

.navi-link {
  color: #000;
  text-align: left;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.navi-link:hover {
  color: #00acdc;
}

.navi-link.is-active {
  color: #00acdc;
  position: relative;
}

.navi-link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.navi-link[data-qa="hover"]:hover {
  color: #00acdc;
}
